import LoaderButton from "./LoaderButton.vue";
export default {
  components: {
    "loader-btn": LoaderButton,
  },
  methods: {
    propagateBackendStatus(value) {
      if (value) {
       this.$emit("backendReady", value);
      }
    }
  }
};
